import pagination from '../layout/pagination';
function news1(){
    if (!document.getElementById('news-1')){
      return;
    }
    var options = {
      queryClassPagination: '.news-1 .box-pagination',
      listBox: '.news-1 .news-1__content  .news-1--padding ',
      
      itemPages: 6,
      scrollTop: {
              id: 'newsListBox',
              header: 'header',
          },
    }
    pagination.pagination(options);
  }
export const news = function(){
   news1();
  }