declare var Swiper : any;


function swiperNews(){
  if (document.querySelector('#news-infor-1__wrapper .news-infor__news--mobile' )){
    var swiper = new Swiper("#news-infor-1__wrapper .swiper-container", {
      // direction: "vertical",
        slidesPerView: 2,
        spaceBetween: 16,
      // lazy: true,
        breakpoints:{
            
           
            540: {
            slidesPerView: 2.6,
            }
        }
     
    })

    
  }
}



export const news_infor = function(){
    swiperNews();
}