import pagination from '../layout/pagination';
function product1(){
    if (!document.getElementById('product1')){
      return;
    }
    var options = {
      queryClassPagination: '.product-1  .box-pagination',
      listBox: '.product-1 .product__showProduct  .box ',
      itemPages: 12,
      scrollTop: {
              id: 'product1  productListBox',
              header: 'header',
          },
    }
    pagination.pagination(options);
  }
  function submitSearchProduct(search: string, year: string, sort: string) {
    var inner = document.querySelector('#product1  .product__showProduct');
    var infor = {
      search: search,
      year: year,
      sort: sort,
    };
    // gọi ajax
    var xhttp = new XMLHttpRequest();
    xhttp.onreadystatechange = function() {
        if (this.readyState == 4 && this.status == 200) {
            inner.innerHTML =this.responseText;
            product1();
            window.history.pushState({ id: 1 }, search, `?search=${search}&year=${year}&sort=${sort}`);
       }
    };
    xhttp.open("POST", "/product_info", true);
    // đổi ojbectt thành chuỗi
    xhttp.send(JSON.stringify(infor));
    // khi gọi thành công 
    // inner.innerHTML =this.responseText;
    // product1();
    
  
    // khi thất bại
    console.log('thất bại');
  
  
  }
  function handleOptionProduct() {
    // if id tung page no con khong thi lỗi
    if (document.getElementById('product1')) {
      var search = <HTMLInputElement>document.getElementById('searchProduct');
      var year = <HTMLInputElement>document.getElementById('cardYear');
      var sort = <HTMLInputElement>document.getElementById('cardSort');
      var btn = document.querySelector('#product1 .product__seclectSearch--button');
      year.addEventListener('change', function () {
        var valYear = this.value;
        var valSort = sort.value;
        var valSearch = search.value;
  
       
        submitSearchProduct(valSearch, valYear, valSort);
      })
      sort.addEventListener('change', function () {
        var valYear = year.value;
        var valSort = this.value;
        var valSearch = search.value;
    
        submitSearchProduct(valSearch, valYear, valSort);
    
      })
    
      btn.addEventListener('click', function (e) {
        e.preventDefault();
    
        var valYear = year.value;
        var valSort = sort.value;
        var valSearch = search.value;
    
        submitSearchProduct(valSearch, valYear, valSort);
    
      })

    }
    
  
  
    
  }
  export const product = function(){
    product1();
    handleOptionProduct();
  }