function swipperthumb() {
  if (document.getElementById('produc-details-1')) {
    var swiper = new Swiper(".product-details-1 .content__slide--thumb .content-img--swiper", {
      slidesPerView: 4,
      spaceBetween: 8,
      // slidesPerGroup: 3,
      // loop: true,
      // loopFillGroupWithBlank: true,
      //   pagination: {
      //     el: ".index-news .swiper-pagination",
      //     // clickable: true,
      //   },
      freeMode: true,
      watchSlidesVisibility: true,
      watchSlidesProgress: true,
      navigation: {
        nextEl: ".product-details-3 .swiper-button-next",
        prevEl: ".product-details-3 .swiper-button-prev",
      },
      breakpoints: {


      }

    });
    new Swiper(".product-details-1 .content__slide--top .content-img--swiper", {
      slidesPerView: 1,
      spaceBetween: 8,
      navigation: {
        nextEl: ".product-details-3 .swiper-button-next",
        prevEl: ".product-details-3 .swiper-button-prev",
      },
      thumbs: {
        swiper: swiper,
      },
      on: {
        init: function () {
          var activeSlide = document.querySelector('.product-details-1 .content__slide--thumb .swiper-slide-active')
          activeSlide.classList.add('active')
          
        },
        transitionStart: function () {
          var activeSlides = document.querySelectorAll('.product-details-1 .content__slide--thumb .swiper-slide')
          activeSlides.forEach(function (item) {
            item.classList.remove('active')
          })

        },
        transitionEnd: function () {
          var activeSlide = document.querySelector('.product-details-1 .content__slide--thumb .swiper-slide-active')
          activeSlide.classList.add('active')
        }
      }
    });

  }
}

function productDetails1(){
  swipperthumb();
}
export const productDetail = function () {
  toggleProductDetails2();
  swipperProductDetails3();
  productDetails1();
  
}