
declare var Swiper: any;


function swipperbanner() {
    if (document.getElementById('banner')) {
        new Swiper(".banner  .banner--swiper", {
            slidesPerView: 1,
            spaceBetween: 0,
            // slidesPerGroup: 3,
            loop: true,
            // loopFillGroupWithBlank: true,
            pagination: {
                el: ".banner  .swiper-pagination",
                dynamicBullets: true,
                // clickable: true,
            },
            autoplay:
            {
                delay: 2000,
            },
            
            // navigation: {
            //   nextEl: ".index-restroom .swiper-button-next",
            //   prevEl: ".index-restroom .swiper-button-prev",
            // },
            breakpoints: {
            }
        });
    }
}


export const banner = function () {
    swipperbanner();

}