function toggleLoginRegister() {
    if (document.getElementById('login-register')) {
        var contentTop = document.querySelectorAll(".login-register  .login-register__title--item");
        var contentBottom = document.querySelectorAll(".login-register  .login-register__form");
        var active = document.querySelector(".login-register  .active");

        if (!contentBottom) {
            return;
        }
        var itemActive = active.getAttribute('data-active');
        contentBottom.forEach(function (itemBot) {
            var itemBotActive = itemBot.getAttribute('data-active');
            var itemBottEle = <HTMLElement>itemBot;
            if (itemActive == itemBotActive) {
                itemBottEle.style.display = "block";
            } else {
                itemBottEle.style.display = "none";
            }

        })

        contentTop.forEach(function (item) {
            item.addEventListener('click', function () {
                var itemActive = item.getAttribute('data-active');

                contentBottom.forEach(function (itemBot) {
                    var itemBotEle = <HTMLElement>itemBot;
                    itemBotEle.style.display = "none";

                })

                contentBottom.forEach(function (itemBot) {
                    var itemBottomActive = itemBot.getAttribute('data-active');
                    var itemBotEle = <HTMLElement>itemBot;
                    if (itemActive == itemBottomActive) {
                        itemBotEle.style.display = "block";
                    }

                })
                contentTop.forEach(function (e) {
                    e.classList.remove("active");
                })

                // them active vao item da click
                item.classList.add("active");
            })
        })

    }
}
function submitLogin(nameLogin: string, passLogin: string) {

    var infor = {
        name: nameLogin,
        pass: passLogin,
    };
    // gọi ajax
    var xhttp = new XMLHttpRequest();
    xhttp.onreadystatechange = function () {
        if (this.readyState == 4 && this.status == 200) {

        }
    };
    xhttp.open("POST", "/infor-login", true);
    // đổi ojbectt thành chuỗi
    xhttp.send(JSON.stringify(infor));
    // khi gọi thành công 
    // inner.innerHTML =this.responseText;
    // product1();


    // khi thất bại
    console.log('thất bại');


}

function getInforlogin() {
    if (document.getElementById('login-register')) {
        var nameLogin = <HTMLInputElement>document.getElementById('loginName');
        var passLogin = <HTMLInputElement>document.getElementById('loginPass');
        console.log(nameLogin.value );
        console.log(nameLogin.value )
        
        var btn = document.getElementById('btn-login');

        btn.addEventListener('click', function (e) {
            e.preventDefault();

            var valName = nameLogin.value;
            var valPass = passLogin.value

           
            submitLogin(valName, valPass);

        })
    }
}

export const loginRegister = function () {
    toggleLoginRegister();
    getInforlogin();
}